// SECTION TEMPLATE
// If you want to add more sections to your page, you can use this component as a template

import React, { useRef, useContext, useEffect } from "react"
import styled from "styled-components"
// import Img from "gatsby-image"

import { motion, useAnimation } from "framer-motion"

import { useOnScreen } from "../../hooks/"
import Context from "../../context/"
import ContentWrapper from "../../styles/ContentWrapper"
import Underlining from "../../styles/Underlining"

// Full Width Section
const StyledSection = styled.section`
  width: 100%;
  height: auto;
  background: ${({ theme }) => theme.colors.background};
  margin-bottom: 1rem;
`

// Fixed width container for content stuff
const StyledContentWrapper = styled(ContentWrapper)`
  && {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
`

// Add more styled components here
const StyledConstruction = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
`

//const Construction = ({ content })
const Construction = () => {
  // Extract GraphQL data here
  //const sectionDetails = content[0].node

  // Animation
  const { isIntroDone } = useContext(Context).state
  const tControls = useAnimation()
  const iControls = useAnimation()

  // Required for animating the text content
  const tRef = useRef()
  const tOnScreen = useOnScreen(tRef)

  // Required for animating the image
  const iRef = useRef()
  const iOnScreen = useOnScreen(iRef)

  // Only trigger animations if the intro is done or disabled
  useEffect(() => {
    if (isIntroDone) {
      if (tOnScreen) tControls.start({ opacity: 1, y: 0 })
      if (iOnScreen) iControls.start({ opacity: 1, x: 0 })
    }
  }, [isIntroDone, tControls, iControls, tOnScreen, iOnScreen])

  return (
    <StyledSection id="construction">
      <StyledContentWrapper>
        <StyledConstruction>
          <motion.div
            ref={tRef}
            initial={{ opacity: 0, y: 20 }}
            animate={tControls}
          >
            <h2>
              The site is still{" "}
              <span role="img" aria-label="Construction Sign Emoji">
                
              </span>{" "}
              <Underlining color="#fdf07f" hoverColor="#fdf07f">
                Under Construction
              </Underlining>{" "}
              <span role="img" aria-label="Construction Sign Emoji">
                
              </span>
            </h2>
            {/* <Img
              className="construction"
              fluid={content.file.childImageSharp.fluid}
            /> */}
            {/* ____SectionContent____ */}
          </motion.div>
        </StyledConstruction>
      </StyledContentWrapper>
    </StyledSection>
  )
}

export default Construction

